.RestaurantsList {
  overflow-y: scroll;
  height: calc(100vh - 150px);
}
@media only screen and (max-width: 820px) and (orientation: landscape) {
  .RestaurantsList {
    height: 100vh;
  }
}

@media only screen and (max-width: 600px) {
  .heading-primary {
    font-size: 40px;
  }
}

/* SCROLLBAR */

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #bbb;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}



