.DynamicInfo {
  overflow-y: scroll;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
  height: 400px;
}
.DynamicInfoOpen {
  -webkit-animation: infoOpen 0.4s ease-out forwards;
          animation: infoOpen 0.4s ease-out forwards;
}
.DynamicInfoClosed {
  -webkit-animation: infoClosed 0.4s ease-out forwards;
          animation: infoClosed 0.4s ease-out forwards;
}

@-webkit-keyframes infoOpen {
  0% {
    visibility: hidden;
    opacity: 0;
    height: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
    height: 400px;
  }
}

@keyframes infoOpen {
  0% {
    visibility: hidden;
    opacity: 0;
    height: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
    height: 400px;
  }
}
@-webkit-keyframes infoClosed {
  0% {
    visibility: visible;
    opacity: 1;
    height: 400px;
  }
  100% {
    visibility: hidden;
    opacity: 0;
    height: 0;
  }
}
@keyframes infoClosed {
  0% {
    visibility: visible;
    opacity: 1;
    height: 400px;
  }
  100% {
    visibility: hidden;
    opacity: 0;
    height: 0;
  }
}



